.message {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.messageTop {
  display: flex;
}

.messageImg {
  width: 32px;
  height: 32px;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 10px;
}

.messageText {
  padding: 10px;
  border-radius: 20px;
  background-color: rgb(247, 244, 244);
  color: black;
  max-width: 300px;
}

.messageBottom {
  font-size: 12px;
  margin-top: 10px;
  color: gray;
}

.message.own {
  align-items: flex-end;
}

.message.own .messageText {
  background-color: rgb(8, 8, 8);
  color: white;
}
